.card{
  box-shadow: 0 10px 10px rgb(0 0 0 / 50%);
}

img {
  width: 100%;
  display: block;
}

.videoContainer {
  position: relative;
}

.playBtn {
  position: absolute;
  width: 75px;
  height: 80px;
  left: 50%;
  top: 50%;
  margin-left: -50px;
  margin-top: -50px;
}

.index {
  z-index: 1000;
}

.img-fluid {
  max-width: 15vw;
  height: auto;
}

.title {
  font-weight: 900;
  font-size: 3vw;
  margin-left: -8vw;
  vertical-align: middle;
}

.channelName {
  margin-left: 8vw;
  margin-bottom: -2vw;
  z-index: 9999;
  font-weight: 900;
  transform: translate(-50%, -50%);
}

.adjustingvm {
  margin-top: -7vw;
  margin-left: -15vw;
}

.fontweight {
  font-weight: 1000;
}

.subTitle {
  margin-top: -1%;
}

