.card-title {
  display: inline-block;
  width: 180px;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

#divContainer .form-control {
  border-radius: 30px;
  border: 1px solid darkblue;
  margin: 0 auto;
  width: 70vw;
}

.w3-tag {
  font-size: x-small;
}

.mAnimatedButton {
  position:absolute;
  overflow:hidden;
  height: 40px;
  bottom: 0;
  z-index: 1;
}







